<template>
    <v-container fluid class="qiong-txt-td">
    
        <qiong-header :extra_calss="'qiong-primay-bg-title pl-7'">團隊獎金</qiong-header>
        
        <qiong-panel-element>
            <div class="qiong-pt-10"></div>

            <bonus-team-layer-table-header></bonus-team-layer-table-header>
            <div class="qiong-pt-5"></div>
            <bonus-team-layer-table :childs="childrens" v-if="childrens != null"></bonus-team-layer-table>

        </qiong-panel-element>
    </v-container>
</template>

<script>
import api_bonus from '../../../common/es/api_bonus/index'

import QiongHeader from '../../../components/Qiong/Pager/QiongHeader.vue'
import QiongPagePanel from '../../../components/Qiong/QiongPagePanel.vue'
import BonusTeamLayerTable from './Inner/BonusTeamLayerTable.vue'
import BonusTeamLayerTableHeader from './Inner/BonusTeamLayerTableHeader.vue'
import QiongPanelElement from '../../../components/Qiong/Ele/QiongPanelElement.vue'


    export default {
        components: {
            QiongHeader,
            QiongPagePanel,
                BonusTeamLayerTableHeader,
                BonusTeamLayerTable,
                QiongPanelElement,
            
        },
        name: '',
        data() {
            return {
                childrens: null
            }
        },
        mounted() {
            this.switchChidren()
        },
        methods: {
            switchChidren() {
                const _this = this
                const many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer
                if (many) {

                    this.childrens = [ ]
                    api_bonus._build_res(many, transfer, function (u, t) {
                        if (_this.$store.state.user_backend.unid != u.unid) {
                            _this.childrens.push(u)
                        }
                    })

                    this.childrens = this.childrens.sort(function(n, o) {
                        return n.index - o.index
                    })
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


