<template>
    <div class="">
        <div class="qiong-td qiong-line-hight-20"
            v-for="(v, i) in childs" :key="i"
            :class="{ 'qiong-td-zebra': v.index % 2 == 0 }"
        >

            <div class="qiong-wide-7">&nbsp;</div>
            <div class="qiong-wide-13">{{ v.username }}</div>
            <div class="qiong-wide-9">{{ v.number_code }}</div>
            <div class="qiong-wide-27">{{ v.display_name }}</div>
            <div class="qiong-wide-5">{{ v.ipv }}</div>
            <div class="qiong-wide-9">
                <level-viewing :item="v.vip.name + '_' + v.vip.star"></level-viewing>
            </div>
            <div class="qiong-wide-10">{{ v.son_pv }}</div>
            <div class="qiong-wide-9">{{ v.son_can_num }}</div>
            <div class="qiong-wide-6">{{ v.bonus.usd_team_layer }}</div>
            <div class="qiong-wide-3">
                <!-- qiong-icon-inset :color="'primary'" :arg="v" @done_Father="openCenter"></qiong-icon-inset -->
            </div>

        </div>
    </div>
</template>

<script>
import QiongIconFlat from '../../../../components/Qiong/Button/QiongIconFlat.vue'
import QiongIconInset from '../../../../components/Qiong/Button/QiongIconInset.vue'
import LevelViewing from '../../../../components/Viewing/LevelViewing.vue'
    export default {
  components: { LevelViewing, QiongIconFlat, QiongIconInset },
        props: {
            childs: {
                type: Array
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        methods: {
            openCenter(user) {
                this.$router.push({ path: '/home/bonus/active_layer_one/' , query: { 'id': user.unid } })
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


